<template>
  <v-container fluid style="height: 100%">
    <v-row style="height: 100%">
      <v-col cols="4" style="height: 100%">
        <div class="some-area fill-height-or-more">
          <v-card elevation="6">
            <v-card-title>{{alertTimeLeft ? "Poplach bol vyhlásený" : "Vyhlásiť poplach"}}</v-card-title>
            <v-card-subtitle>{{alertTimeLeft ? `Systém automaticky upozorní novo-pripojených členov po dobu ${alertTimeLeft} minút` : "Upozornť všetkých členov"}}</v-card-subtitle>
            <v-card-text>
              <v-btn
                outlined
                color="error"
                large
                @click.stop="dialog = true"
                :disabled="!connected"
              >Poplach!</v-btn>
            </v-card-text>
          </v-card>
          <v-card
            style="margin-top: 12px; flex: 1;"
            class="custom-v-card"
            elevation="6"
            :loading="connected"
          >
            <v-card-title>Pripojení</v-card-title>
            <v-card-subtitle
              v-if="connected"
            >Počet online: {{connected ? connectedMembers.length : "..."}}</v-card-subtitle>
            <v-card-text>
              <v-list dense v-if="connected">
                <v-list-item style="padding: 0" v-for="item in connectedMembers" :key="item.id">
                  <v-list-item-avatar :color="stringToHslColor(item.name)" size="36">
                    <span class="white--text">{{item.name.split(" ").map((n)=>n[0]).join("")}}</span>
                  </v-list-item-avatar>

                  <v-list-item-content>
                    <v-list-item-title v-text="item.name"></v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list>
              <v-skeleton-loader
                v-else
                class="mx-auto"
                type="list-item-avatar-two-line, list-item-avatar, list-item-avatar-three-line"
              ></v-skeleton-loader>
            </v-card-text>
          </v-card>
        </div>
      </v-col>
      <v-col cols="8" style="height: 100%">
        <div class="some-area fill-height-or-more">
          <v-card class="custom-v-card" elevation="6">
            <v-card-title>
              Prídem
              <v-spacer></v-spacer>
              <v-btn icon @click="clearAnswers()">
                <v-icon>mdi-broom</v-icon>
              </v-btn>
            </v-card-title>
            <v-card-subtitle>Počet účastníkov: {{connected ? participants.length : "..."}}</v-card-subtitle>
            <v-card-text>
              <v-list dense v-if="connected && participants.length !== 0">
                <v-list-item style="padding: 0" v-for="item in participants" :key="item.id">
                  <v-list-item-avatar :color="stringToHslColor(item.name)" size="36">
                    <span class="white--text">{{item.name.split(" ").map((n)=>n[0]).join("")}}</span>
                  </v-list-item-avatar>

                  <v-list-item-content>
                    <v-list-item-title v-text="item.name"></v-list-item-title>
                    <v-list-item-subtitle v-text="item.message"></v-list-item-subtitle>
                  </v-list-item-content>
                </v-list-item>
              </v-list>
              <v-skeleton-loader v-else class="mx-auto" type="list-item-avatar-three-line"></v-skeleton-loader>
            </v-card-text>
          </v-card>
          <v-card class="custom-v-card" elevation="6">
            <v-card-title>Neprídem</v-card-title>
            <v-card-subtitle>Nezúčastní sa: {{connected ? nonparticipants.length : "..."}}</v-card-subtitle>
            <v-card-text>
              <v-list dense v-if="connected && nonparticipants.length !== 0">
                <v-list-item style="padding: 0" v-for="item in nonparticipants" :key="item.id">
                  <v-list-item-avatar :color="stringToHslColor(item.name)" size="36">
                    <span class="white--text">{{item.name.split(" ").map((n)=>n[0]).join("")}}</span>
                  </v-list-item-avatar>

                  <v-list-item-content>
                    <v-list-item-title v-text="item.name"></v-list-item-title>
                    <v-list-item-subtitle v-text="item.message"></v-list-item-subtitle>
                  </v-list-item-content>
                </v-list-item>
              </v-list>
              <v-skeleton-loader v-else class="mx-auto" type="list-item-avatar-three-line"></v-skeleton-loader>
            </v-card-text>
          </v-card>
          <v-card class="custom-v-card" elevation="6">
            <v-card-title>Čaká sa na odpoveď</v-card-title>
            <v-card-subtitle>Neodpovedajú: {{connected ? pendingMembers.length : "..."}}</v-card-subtitle>
            <v-card-text>
              <v-list dense v-if="connected && pendingMembers.length !== 0">
                <v-list-item style="padding: 0" v-for="item in pendingMembers" :key="item.id">
                  <v-list-item-avatar :color="stringToHslColor(item.name)" size="36">
                    <span class="white--text">{{item.name.split(" ").map((n)=>n[0]).join("")}}</span>
                  </v-list-item-avatar>

                  <v-list-item-content>
                    <v-list-item-title v-text="item.name"></v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list>
              <v-skeleton-loader v-else class="mx-auto" type="list-item-avatar"></v-skeleton-loader>
            </v-card-text>
          </v-card>
        </div>
      </v-col>
    </v-row>

    <v-dialog v-model="dialog" max-width="500">
      <v-card>
        <v-card-title class="headline">Naozaj chcete vyhlásiť poplach?</v-card-title>
        <v-card-text>
          Všetci pripojení členovia budú ihneď upozornení. Systém upozorní aj členov ktorý sa pripoja po vyhlásení poplachu a to po dobu {{gap}} minút.
          <br />Nižšie môžete pridať krátku správu.
        </v-card-text>
        <v-card-text>
          <v-textarea v-model="customMessage" outlined label="Správa"></v-textarea>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" text @click="alertConnected()">Vyhlásiť</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialog2" max-width="800">
      <v-card>
        <v-card-title class="headline">Nastavenia</v-card-title>
        <v-card-text>
          <v-card class="mx-auto" outlined>
            <v-list-item three-line>
              <v-list-item-content>
                <div class="overline mb-4">PARAMETRE</div>
                <v-list-item-subtitle>Prispôsobte systém vašim potrebám</v-list-item-subtitle>
                <div>
                  <v-slider
                    v-model="slider"
                    label="Doba automatického upozornenia (v minútach)"
                    min="0"
                    max="15"
                    step="1"
                    thumb-label="always"
                    :thumb-size="24"
                    hide-details
                    ticks
                  ></v-slider>
                  <v-switch
                    v-model="toggle"
                    persistent-hint
                    label="Automatické vyčistenie odpovedí"
                    hint="Vymaže odpovede po vyhlásení nového poplachu"
                  ></v-switch>
                </div>
              </v-list-item-content>
            </v-list-item>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="error"
                text
                :disabled="slider == gap && toggle == autoClear"
                @click="changeParameters()"
              >Uložiť</v-btn>
            </v-card-actions>
          </v-card>
        </v-card-text>
        <v-card-text>
          <v-card class="mx-auto" outlined>
            <v-list-item three-line>
              <v-list-item-content>
                <div class="overline mb-4">PLATNOSŤ</div>
                <v-list-item-subtitle>Predĺžte platnosť skupiny (platnosť vyprší {{roomDetails.expiration ? new Date(Number(roomDetails.expiration)).toLocaleDateString() : "..."}})</v-list-item-subtitle>
                <v-slider
                  v-model="months"
                  :hint="sum"
                  persistent-hint
                  label="Počet mesiacov"
                  min="1"
                  max="12"
                  step="1"
                  thumb-label="always"
                  :thumb-size="24"
                  ticks
                ></v-slider>
              </v-list-item-content>
            </v-list-item>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="error" text disabled>Predplatiť</v-btn>
            </v-card-actions>
          </v-card>
        </v-card-text>
        <v-card-text>
          <v-card class="mx-auto" outlined>
            <v-list-item three-line>
              <v-list-item-content>
                <div class="overline mb-4">PRÍSTUP</div>
                <v-list-item-subtitle class="mb-2">Zmena prístupového hesla</v-list-item-subtitle>
                <v-form ref="form" v-model="valid">
                  <v-text-field
                    filled
                    rounded
                    dense
                    v-model="newPassword"
                    :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                    :rules="[rules.required, rules.min]"
                    :type="show ? 'text' : 'password'"
                    label="Nové heslo miestnosti"
                    hint="Slúži na vstup do miesnosti"
                    @click:append="show = !show"
                  ></v-text-field>
                </v-form>
              </v-list-item-content>
            </v-list-item>
            <v-card-actions>
              <v-spacer></v-spacer>
              <!-- <v-btn color="error" text :disabled="!valid">Zmeniť heslo</v-btn> -->
              <v-btn color="error" text disabled>Zmeniť heslo</v-btn>
            </v-card-actions>
          </v-card>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
export default {
  props: ["settingsDialogState"],
  data: () => ({
    currentMillis: Date.now(),
    gap: localStorage.getItem("gap") || 5,
    slider: localStorage.getItem("gap") || 5,
    autoClear: JSON.parse(localStorage.getItem("autoClear")) || true,
    toggle: JSON.parse(localStorage.getItem("autoClear")) || true,
    wsPingInterval: null,
    wsReconnectTimeout: null,
    socket: null,
    connected: false,
    token: null,
    wsServer: sessionStorage.getItem("wsServer"),
    roomDetails: { room: null, expiration: null },
    dialog: false,
    months: 6,
    newPassword: "",
    show: false,
    rules: {
      required: v => !!v || "Povinné",
      min: v => v.length >= 8 || "Minimálne 8 znakov"
    },
    valid: false,
    customMessage: "",
    connectedMembers: [],
    participants: [],
    nonparticipants: [],
    pendingMembers: []
  }),
  computed: {
    dialog2: {
      get: function() {
        return this.settingsDialogState;
      },
      set: function(newValue) {
        this.$emit("settingsDialogState", newValue);
      }
    },
    sum() {
      if (this.months === 12)
        return `Spolu: ${(10 * this.months * 0.85).toFixed(2)}€ (zľava 15%)`;
      else if (this.months >= 6)
        return `Spolu: ${(10 * this.months * 0.9).toFixed(2)}€ (zľava 10%)`;
      else if (this.months >= 3)
        return `Spolu: ${(10 * this.months * 0.95).toFixed(2)}€ (zľava 5%)`;
      else return `Spolu: ${(10 * this.months).toFixed(2)}€`;
    },
    alertTimeLeft() {
      if (sessionStorage.getItem("lastAlert")) {
        let s = (
          (JSON.parse(sessionStorage.getItem("lastAlert")).timestamp +
            60000 * (Number(localStorage.getItem("gap")) || 5) -
            this.currentMillis) /
          1000
        ).toFixed(0);
        if (s >= 0) return (s - (s %= 60)) / 60 + (9 < s ? ":" : ":0") + s;
        else return null;
      } else return null;
    }
  },
  created: function() {
    this.connect();
    setInterval(() => (this.currentMillis = Date.now()), 1000);
  },
  methods: {
    connect: function() {
      this.token = sessionStorage.getItem("authToken");
      if (this.token) {
        // this.socket = new WebSocket("ws://68.183.71.163");
        this.socket = new WebSocket(`wss://api.${this.wsServer}.hasime.sk`);

        this.socket.onopen = () => {
          this.socket.send(
            JSON.stringify({
              action: "joinAdmin",
              token: this.token
            })
          );
          this.$emit("socketStatus", this.socket.readyState);
          this.roomDetails = this.$jwt_decode(
            sessionStorage.getItem("authToken")
          );
          this.$emit("login", this.roomDetails);
          this.connected = true;
          clearInterval(this.wsPingInterval);
          this.wsPingInterval = setInterval(this.ping, 25000);
        };

        this.socket.onmessage = message => {
          const json = JSON.parse(message.data);
          switch (json.action) {
            case "roomUpdate": {
              console.log(json.room);
              this.connectedMembers = json.room.filter(
                e => e.isAdmin === false
              );
              break;
            }
            case "newConnection": {
              const newUser = json.socket;
              console.log("connection", newUser.name);
              if (!this.connectedMembers.some(e => e.name === newUser.name))
                this.connectedMembers.unshift(newUser);

              if (
                sessionStorage.getItem("lastAlert") &&
                Date.now() -
                  JSON.parse(sessionStorage.getItem("lastAlert")).timestamp <
                  60000 * (Number(localStorage.getItem("gap")) || 5)
              ) {
                if (
                  !this.participants.some(e => e.name === newUser.name) &&
                  !this.nonparticipants.some(e => e.name === newUser.name)
                ) {
                  this.socket.send(
                    JSON.stringify({
                      action: "alertOne",
                      id: newUser.id,
                      message: JSON.parse(sessionStorage.getItem("lastAlert"))
                        .message
                    })
                  );
                  if (!this.pendingMembers.some(e => e.name === newUser.name))
                    this.pendingMembers.unshift(newUser);
                }
              }
              break;
            }
            case "newDisconnection": {
              console.log("disconnection", json.socket.name);
              this.connectedMembers = this.connectedMembers.filter(
                e => e.name !== json.socket.name
              );
              break;
            }
            case "answer": {
              this.pendingMembers = this.pendingMembers.filter(
                e => e.name !== json.name
              );
              if (json.participation) {
                const index = this.participants.findIndex(
                  e => e.name === json.name
                );
                if (index === -1) {
                  this.participants.unshift({
                    id: json.id,
                    name: json.name,
                    message: json.message
                  });
                } else this.participants[index].message = json.message;
              } else {
                const index = this.nonparticipants.findIndex(
                  e => e.name === json.name
                );
                if (index === -1) {
                  this.nonparticipants.unshift({
                    id: json.id,
                    name: json.name,
                    message: json.message
                  });
                } else this.nonparticipants[index].message = json.message;
              }
              break;
            }
            case "error": {
              sessionStorage.clear();
              this.$router.push("/login", () => {});
              break;
            }
          }
        };

        this.socket.onclose = () => {
          this.$emit("socketStatus", this.socket.readyState);
          this.connected = false;
          console.log(
            "Socket is closed. Reconnect will be attempted in 10 second."
          );
          clearTimeout(this.wsReconnectTimeout);
          this.wsReconnectTimeout = setTimeout(() => {
            console.log("Trying to reconnect...");
            this.socket.close();
            this.connect();
          }, 5000);
        };

        this.socket.onerror = () => {
          this.$emit("socketStatus", this.socket.readyState);
          this.connected = false;
          this.socket.close();
        };
      } else this.$router.push("/login");
    },
    ping: function() {
      if (this.connected)
        this.socket.send(
          JSON.stringify({
            action: "ping"
          })
        );
    },
    stringToHslColor: function(str, s = 50, l = 50) {
      let hash = 0;
      for (let i = 0; i < str.length; i++) {
        hash = str.charCodeAt(i) + ((hash << 5) - hash);
      }

      const h = hash % 360;
      return "hsl(" + h + ", " + s + "%, " + l + "%)";
    },
    alertConnected: function() {
      if (this.autoClear) this.clearAnswers();
      this.socket.send(
        JSON.stringify({
          action: "alert",
          message: `${this.customMessage}`
        })
      );
      sessionStorage.setItem(
        "lastAlert",
        JSON.stringify({
          timestamp: Date.now(),
          message: `${this.customMessage}`
        })
      );

      this.pendingMembers = this.connectedMembers;
      this.dialog = false;
      this.customMessage = "";
    },
    clearAnswers: function() {
      this.participants = [];
      this.nonparticipants = [];
      this.pendingMembers = [];
    },
    changeParameters: function() {
      this.gap = this.slider;
      localStorage.setItem("gap", this.slider);
      this.autoClear = this.toggle;
      localStorage.setItem("autoClear", this.toggle);
    }
  }
};
</script>

<style>
.fill-height-or-more {
  max-height: 100%;
  display: flex;
  flex-direction: column;
}
.fill-height-or-more > .custom-v-card {
  flex: 1;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  justify-content: top;
}

.some-area > .custom-v-card:nth-child(2) {
  margin-top: 12px;
}
.some-area > .custom-v-card:nth-child(3) {
  margin-top: 12px;
}

.v-input--selection-controls {
  margin-top: 0px !important;
}

/* Hide scrollbar for Chrome, Safari and Opera */
* ::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE and Edge */
* {
  -ms-overflow-style: none;
}
</style>
